* {
  overflow-wrap: break-word;
  overflow: auto;
}

body {
  margin: 0;
  box-sizing: border-box;
  padding-top: 2rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #e3fdfd;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.greeting,
.subtitle {
  color: #ff5555;
}
.greeting {
  margin: 1.2em 0 0;
  font-size: 1.8em;
}
.subtitle {
  margin: 1em 0 0;
}

.formList {
  padding: 1.6em 1em 0.8em;
  position: relative;
  box-sizing: border-box;
}

.formControl {
  width: 100%;
  flex-direction: row !important;
  justify-content: space-between;
}

.italic {
  font-style: italic !important;
}
.formInput {
  flex: 1 1;
}
.todo-list {
  border-radius: 2em;
  cursor: pointer;
}
.todo-list-item {
  padding: 0 1em !important;
}

.todo-list:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.App {
  position: relative;
  box-sizing: border-box;

  max-width: 480px;
  width: 100%;
  height: auto;
  padding: 1rem;
  margin: 1em auto;
  text-align: center;

  background: #fff;
  border-radius: 0.75rem;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
/* RESET
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul,
li {
  list-style: none;
}

